import {makeAutoObservable, reaction} from "mobx";
import i18n from "./i18n";

class AppStore {
    locale

    constructor() {
        makeAutoObservable(this)
    }
}

const appStore = new AppStore();
export default appStore;

reaction(
    () => appStore.locale,
    locale => {
        i18n.changeLanguage(locale);
    }
);