import {Container, List, ListItem, ListItemText, makeStyles, Toolbar} from "@material-ui/core";
import React from "react";
import {useTranslation} from "react-i18next";

const createData = (id, year, name, link, category) => {
    return {id, year, name, link: process.env.PUBLIC_URL + link, category}
}

const useStyles = makeStyles({
    link: {
        textDecoration: "none",
        color: "blue"
    },
})

const Achievements = (props) => {
    const [t] = useTranslation();
    const classes = useStyles()
    const rows = [
        createData(1, 2016, t('ACM ICPC'), "docs/ACM.pdf", t('olympiad')),
        createData(2, 2017, t('Atos summer school'), "docs/Atos_2017.pdf", t('summer school')),
        createData(7, 2019, t('dbt'), "docs/dbt_2019.pdf", t('hackathon')),
        createData(12, 2020, t('dbt'), "docs/dbt_2020.pdf", t('hackathon')),
        createData(13, 2020, t('OpenSpace hackathon'), "docs/openspace.pdf", t('hackathon')),
        createData(9, 2019, t('FIEB'), "docs/FIEB.pdf", t('other')),
        createData(6, 2018, t('Severstal hackathon'), "docs/severstal_hack.pdf", t('hackathon')),
        createData(10, 2019, t('Tools 50+1'), "docs/tools.pdf", t('hackathon')),
        createData(11, 2019, t('Best student group UI'), "docs/ui_diploma.pdf", t('other')),
        createData(8, 2019, t('VrnCTF'), "docs/vrnctf19.pdf", t('olympiad')),
        createData(3, 2017, t('Olympiad in web programming'), "docs/web_2017.pdf", t('olympiad')),
        createData(4, 2018, t('Olympiad in web programming'), "docs/web_2018.pdf", t('olympiad')),
        createData(5, 2018, t('World skills Russia'), "docs/World Skills.pdf", t('olympiad'))
    ].sort(function (a, b) {
        if (a.year < b.year)
            return -1
        if (a.year > b.year)
            return 1
        if (a.year === b.year) {
            if (a.category > b.category)
                return 1
            if (a.category < b.category)
                return -1
            if (a.category === b.category) {
                if (a.name > b.name)
                    return 1
                if (a.name < b.name)
                    return -1
                return 0
            }
        }
    })

    return (
        <>
            <Toolbar id="back-to-top-anchor"/>
            <Container maxWidth="md">
                <List>
                    {
                        rows.map((row) => (
                                <ListItem>
                                    <ListItemText>
                                        {row.name} ({row.year})&nbsp;
                                        <a className={classes.link} href={row.link} target="_blank">{t('view')}</a>
                                    </ListItemText>
                                </ListItem>
                            )
                        )
                    }
                </List>
            </Container>
        </>
    )
}

export default Achievements;