import {
    Container,
    Paper,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
    Toolbar
} from "@material-ui/core";
import React from "react";
import {useTranslation} from "react-i18next";

const createData = (id, years, institution, speciality, level) => {
    return {id, years, institution, speciality, level}
}

const Education = (props) => {
    const [t] = useTranslation();
    const rows = [
        createData(1, '2011-2015', t('MMMC PSTU'), t('software development'), t('junior specialist')),
        createData(2, '2015-2019', t('VSUET'), t('information systems and technologies'), t('BSc')),
        createData(3, '2019-2021', t('IU'), t('CS DS'), t('MSc'))
    ]

    return (
        <>
            <Toolbar id="back-to-top-anchor"/>
            <Container maxWidth="md">
                <TableContainer component={Paper}>
                    <Table size="medium" aria-label="a dense table" border={1}>
                        <TableHead>
                            <TableRow>
                                <TableCell>
                                    #
                                </TableCell>
                                <TableCell width={"12%"}>
                                    {t('years')}
                                </TableCell>
                                <TableCell>
                                    {t('institution')}
                                </TableCell>
                                <TableCell>
                                    {t('speciality')}
                                </TableCell>
                                <TableCell width={"20%"}>
                                    {t('level')}
                                </TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {
                                rows.map((row) => (
                                    <TableRow key={row.id}>
                                        <TableCell component="th" scope="row">
                                            {row.id}
                                        </TableCell>
                                        <TableCell>
                                            {row.years}
                                        </TableCell>
                                        <TableCell>
                                            {row.institution}
                                        </TableCell>
                                        <TableCell>
                                            {row.speciality}
                                        </TableCell>
                                        <TableCell>
                                            {row.level}
                                        </TableCell>
                                    </TableRow>
                                ))
                            }
                        </TableBody>
                    </Table>
                </TableContainer>
            </Container>
        </>
    )
}

export default Education;