import {Trans, useTranslation} from "react-i18next";
import {Avatar, Container, Fab, Grid, List, ListItem, makeStyles, Paper, Toolbar} from "@material-ui/core";
import KeyboardArrowUpIcon from '@material-ui/icons/KeyboardArrowUp';
import React from "react";
import ScrollTop from "./ScrollTop";
import ListItemText from "@material-ui/core/ListItemText";
import TelegramIcon from '@material-ui/icons/Telegram';
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import GitHubIcon from '@material-ui/icons/GitHub';

const useStyles = makeStyles((theme) => ({
    personCard: {
        margin: 10,
        padding: 10,
    },
    personAvatar: {
        margin: 'auto'
    },
    flex: {
        display: "flex"
    },
    flexList: {
        display: "flex",
        justifyContent: "space-between"
    },
    large: {
        width: theme.spacing(7),
        height: theme.spacing(7),
    },
    gridItem: {
        width: `${100 / 3}%`,
        minHeight: '100%'
    },
    gridItemPaper: {
        minHeight: "inherit",
        textAlign: "center",
        [theme.breakpoints.down('md')]: {
            wordBreak: "break-word"
        }
    },
    boldText: {
        fontWeight: "bold"
    },
    link: {
        textDecoration: "none",
        color: "blue"
    },
    title: {
        fontFamily: "'titilliumlight_upright',Arial,sans-serif"
    }
}))

const Main = (props) => {
    const [t] = useTranslation();
    const classes = useStyles()

    return (
        <>
            <Toolbar id="back-to-top-anchor"/>
            <Container maxWidth="xl">
                <Grid container justify="center" spacing={0}>
                    <Grid xs item className={classes.gridItem}>
                        <Paper className={classes.gridItemPaper}>
                            <h2 className={classes.title}>
                                {t('Who am I?')}
                            </h2>
                            <Avatar className={`${classes.personAvatar} ${classes.large}`}
                                    variant="rounded"
                                    alt={t('Bakaleynik Sergey')}
                                    src={process.env.PUBLIC_URL + "images/photo.jpeg"}/>
                            <span>{t('Bakaleynik Sergey')}</span>
                            <div>
                                <Trans>
                                    A talented Data Scientist / Python developer Located in Russia
                                </Trans>
                            </div>
                        </Paper>
                    </Grid>
                    <Grid xs item className={classes.gridItem}>
                        <Paper className={classes.gridItemPaper} component={Container}>
                            <h2 className={classes.title}>
                                {t('Personal info')}
                            </h2>
                            <List>
                                <ListItem>
                                    <ListItemText>
                                        <span className={classes.boldText}>{t('birthdate')}</span>: 02.08.1995
                                    </ListItemText>
                                </ListItem>
                                <ListItem>
                                    <ListItemText>
                                        <span className={classes.boldText}>{t('phone')}</span>:&nbsp;
                                        <a className={classes.link} href="tel:+79009510113">+79009510113</a>
                                    </ListItemText>
                                </ListItem>
                                <ListItem>
                                    <ListItemText>
                                        <span className={classes.boldText}>{t('email')}</span>:&nbsp;
                                        <a className={classes.link} href='mailto:s.jr.bakaleynik@gmail.com'
                                           target="_blank"
                                           rel="noreferrer">s.jr.bakaleynik@gmail.com</a>
                                    </ListItemText>
                                </ListItem>
                                <ListItem>
                                    <ListItemText>
                                        <span className={classes.boldText}>{t('employment')}</span>: {t('full time')}
                                    </ListItemText>
                                </ListItem>
                                <ListItem>
                                    <ListItemText>
                                        <span className={classes.boldText}>{t('work schedule')}</span>:&nbsp;
                                        <Trans>
                                            flexible schedule, full day, remote working
                                        </Trans>
                                    </ListItemText>
                                </ListItem>
                            </List>
                            <Grid container justify="center" spacing={0}>
                                <Grid xs item className={classes.gridItem}>
                                    <a className={classes.link} href="https://t.me/BSergey_jr" target="_blank"
                                       rel="noreferrer"><TelegramIcon/></a>
                                </Grid>
                                <Grid xs item className={classes.gridItem}>
                                    <a className={classes.link}
                                       href="https://www.linkedin.com/in/sergey-bakaleynik-48aa83130/" target="_blank"
                                       rel="noreferrer"><LinkedInIcon/></a>
                                </Grid>
                                <Grid xs item className={classes.gridItem}>
                                    <a className={classes.link}
                                       href="https://github.com/sergey-jr" target="_blank"
                                       rel="noreferrer"><GitHubIcon/></a>
                                </Grid>
                            </Grid>
                        </Paper>
                    </Grid>
                    <Grid xs item className={classes.gridItem}>
                        <Paper className={classes.gridItemPaper}>
                            <h2 className={classes.title}>
                                {t('My Skills')}
                            </h2>
                            <List>
                                <ListItem>
                                    <ListItemText>
                                        <span
                                            className={classes.boldText}>{t('languages')}</span>:&nbsp;
                                        <Trans>
                                            Russian (native), English (C1)
                                        </Trans>
                                    </ListItemText>
                                </ListItem>
                                <ListItem>
                                    <ListItemText>
                                        <span
                                            className={classes.boldText}>{t('Hard Skills')}</span>:
                                        Python, JS, HTML, CSS, SQL (PgSQL), {t('math')}
                                    </ListItemText>
                                </ListItem>
                                <ListItem>
                                    <ListItemText>
                                        <span className={classes.boldText}>{t('frameworks/libraries')}</span>:
                                        <List>
                                            <ListItem>
                                                Python (web): Django/Django-material, Django-rest, flask
                                            </ListItem>
                                            <ListItem>
                                                Python (ML): tensorflow, keras, sklearn, xgboost, pandas, numpy
                                            </ListItem>
                                            <ListItem>
                                                JS: React, jquery, axios
                                            </ListItem>
                                        </List>
                                    </ListItemText>
                                </ListItem>
                                <ListItem>
                                    <ListItemText>
                                        <span className={classes.boldText}>{t('Soft skills')}</span>:&nbsp;
                                        <Trans>
                                            Communication, teamwork, time management, flexibility, stress tolerance
                                        </Trans>
                                    </ListItemText>
                                </ListItem>
                            </List>
                        </Paper>
                    </Grid>
                </Grid>
            </Container>
            <ScrollTop {...props}>
                <Fab color="secondary" size="small" aria-label="scroll back to top">
                    <KeyboardArrowUpIcon/>
                </Fab>
            </ScrollTop>
        </>
    )
}

export default Main;