import {
    Container,
    Paper,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
    Toolbar
} from "@material-ui/core";
import React from "react";
import {useTranslation} from "react-i18next";

const createData = (id, dates, organization, position) => {
    return {id, dates, organization, position}
}

const Work = (props) => {
    const [t] = useTranslation();
    const rows = [
        createData(1, "01.10.2017-01.07.2019", t("LLC \"IT-Russ\""), t('Python developer'))
    ]

    return (
        <>
            <Toolbar id="back-to-top-anchor"/>
            <Container maxWidth="md">
                <TableContainer component={Paper}>
                    <Table size="medium" aria-label="a dense table" border={1}>
                        <TableHead>
                            <TableRow>
                                <TableCell>
                                    #
                                </TableCell>
                                <TableCell width={"12%"}>
                                    {t('dates')}
                                </TableCell>
                                <TableCell>
                                    {t('organization')}
                                </TableCell>
                                <TableCell>
                                    {t('position')}
                                </TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {
                                rows.map((row) => (
                                    <TableRow key={row.id}>
                                        <TableCell component="th" scope="row">
                                            {row.id}
                                        </TableCell>
                                        <TableCell>
                                            {row.dates}
                                        </TableCell>
                                        <TableCell>
                                            {row.organization}
                                        </TableCell>
                                        <TableCell>
                                            {row.position}
                                        </TableCell>
                                    </TableRow>
                                ))
                            }
                        </TableBody>
                    </Table>
                </TableContainer>
            </Container>
        </>
    )
}

export default Work